import React from 'react';
import HomePage from './pages/HomePage';
import WorkPage from './pages/Workpage';
import Project01 from './pages/Project01';
import Project02 from './pages/Project02';
import Project03 from './pages/Project03';
import Project04 from './pages/Project04';
import Project05 from './pages/Project05';
import Header from './components/Header';
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import './App.css';
import AboutPage from './pages/AboutPage';

function App() {
  return (
    <main>
        <BrowserRouter>
        <Header/>
          <Switch>
              <Route exact path="/" component={HomePage}/>
              <Route path="/work" component={WorkPage}/>
              <Route path="/work/finance" component={Project01}/>
              <Route path="/work/retail" component={Project02}/>
              <Route path="/work/groceryshopping" component={Project03}/>
              <Route path="/work/sales" component={Project04}/>
              <Route path="/about" component={AboutPage}/>
              <Route component={HomePage}/>
            </Switch>
        </BrowserRouter>
    </main>
  );
}

export default App;
