import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';


const TagGroup = (props) => {
    return (
        <div className="tagGroup">
            {props.meta.map((item, index) => {
                return(
                    <span key={index}>{item}</span>
                )
            })}
        </div>
    )
}

const Thumbnail = (props) => {
    const length = props.images.length;

    return(
        <NavLink className="card" to={props.path}>
            <div>
                <h3>{props.title}</h3>
                <TagGroup meta={props.meta}/>
            </div>
            {/* <div>
                {props.images.map((image, index) => {
                    return(
                    <img key={index} src={image} className={length > 1 ? "desktop" : "mobile"}/>
                    )
                })}
            </div> */}
        </NavLink>
    )
}

const ThumbnailBlock = (props) => {
    const heroContent = props.heroContent;

    return (
        <div className="container column">
            <h4>{heroContent.subTitle}</h4>
            <h2>{heroContent.title}</h2>
            <section className="grid">
                {props.thumbnails.map((thumbnail, index) => {
                    return (
                    <Thumbnail key={index}
                    title={thumbnail.title} 
                    images={thumbnail.images}
                    meta={thumbnail.meta}    
                    path={thumbnail.path}
                    />
                    )
                })}
            </section>
        </div>
    )
}

export default ThumbnailBlock;