import React, { Component } from 'react';

class Notification extends Component {

    state = {
        isVisible: true,
    }

    setInvisible = () => {
        this.setState(prevState => (
            {
                isVisible: !prevState
            }
            ));
    }

    render() {
        const visible = this.state.isVisible;

        return(
            <div className={`notification ${visible ? "" : "slide-out"}`}>
                <div className="container twothird">
                    <p>{"The website is currently under re-construction. All portfolio cases will be available again soon."}</p>
                    <button className="outline" onClick={this.setInvisible}>{ "Close"}</button>
                </div>
            </div>
        )
    }
}

export default Notification;